@mixin Big-title() {
  font-family: 'Gagalin', Arial, sans-serif;
  font-size: 72px;
  line-height: 64px;
  text-transform: uppercase;
}

@function rem($p) {
  @return $p / 16px+0rem;
}

@mixin tablets {
  @media screen and (max-width: $tablets){
    @content;
  }
}
@mixin phones {
  @media screen and (max-width: $phones){
    @content;
  }
}


